.App {
  background: #ececec;
}
.App > main {
  padding: 10px 20px;
}
.App > main .memory-box > .ant-card-body {
  padding: 10px;
}
.App > footer {
  height: 50px;
  margin-top: 10px;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.App .ant-card {
  margin-top: 10px;
}
.App .App-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 20px;
  background: #fff;
}
.App .App-header > img {
  width: 40px;
  height: 40px;
}
.App .App-header > h2 {
  margin-left: 8px;
  margin-bottom: 0;
  color: #1296dc;
}
.App .ant-fullcalendar-header > .ant-radio-group {
  display: none;
}
.diary-header {
  height: 80px;
}
.diary-header .ant-card-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  padding: 0 24px;
}
.diary-header .ant-card-body > div:first-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 80px;
}
.diary-header .ant-card-body > div:first-child > h2 {
  margin-bottom: 0;
}
.diary-header .ant-card-body > div:first-child > span {
  margin-left: 5px;
  line-height: 1.2;
}
.diary-header .ant-card-body > div:first-child > span > p {
  width: 100px;
  margin-bottom: 0;
}
.diary-header .ant-card-body > div:last-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-left: 80px;
}
.diary-header .ant-card-body > div:last-child > span {
  margin-left: 5px;
}
.ant-card:not(:first-child) {
  margin-top: 10px;
}
.ant-card:not(:first-child) .ant-card-body {
  padding: 0;
}
.ant-card:not(:first-child) .ant-card-body > textarea {
  padding: 5px;
}
.ant-card:not(:first-child) .ant-input {
  border: .5px solid #e8e8e8;
}



